import Vue from 'vue';
import Vuex from 'vuex';
import axios from "axios";

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isPassChecked : false,
    ip : null,
    isConnected : null,
    user : {
      userEmail : null,
      suscribtion : null,
    }
  },
  getters : {
    isConnected(state) {
      return state.isConnected;
    },
    suscribtion (state) {
      return state.user.suscribtion
    },
    userEmail(state) {
      return state.user.userEmail
    }
  },
  mutations: {
    getInfo(state, data) {
      state.user = data
    }
  },
  actions: {
    getInfo({commit}) {
      axios
        .get('/api/user/info')
        .then(response => {
          if (!response.data.error) {
          commit('getInfo', response.data.user)
          } 
        })
        .catch(error => console.log(error));
    }
  }
})
