<template>
  <div>
    <button class="button" @click="isModal = true">Create a room</button>
    <div :class="['modal', {'is-active' : isModal}]">
      <div class="modal-background"></div>
      <div class="modal-content">
        <!-- Any other Bulma elements you want -->
        <transition name="custom-fade-in-right" enter-active-class="animated bounceInLeft">
          <div class="box" v-if="isModal">

            <div class="flex justify center" v-if="suscribtion !== 'true' ">
            <p> <strong>Wanna create more than only one room per session ?</strong> </p>
            <router-link tag="button" to="/pricing" class="button is-warning"><i class="em em-rocket"></i>Upgrade now </router-link>
            </div>

            <p class="has-text-centered" :style="{color : 'red'}">{{errorMessage}}</p>
            <br />

            <form @submit.prevent="validateBeforeSubmit" autocomplete="off">
              <b-field label="Username">
                <p class="control has-icon has-icon-right">
                  <input
                    name="username"
                    v-model="username"
                    v-validate="'required'"
                    :class="{'input': true, 'is-danger': errors.has('username') }"
                    type="text"
                    placeholder="username"
                  />
                  <i v-show="errors.has('username')" class="fa fa-warning"></i>
                  <span
                    v-show="errors.has('username')"
                    class="help is-danger"
                  >You must entrer an username</span>
                </p>
              </b-field>

              <b-field label="Name of the room">
                <p class="control has-icon has-icon-right">
                  <input
                    name="roomName"
                    v-model="roomName"
                    v-validate="'required'"
                    :class="{'input': true, 'is-danger': errors.has('roomName') }"
                    type="text"
                    placeholder="name of the room"
                  />
                  <i v-show="errors.has('roomName')" class="fa fa-warning"></i>
                  <span
                    v-show="errors.has('roomName')"
                    class="help is-danger"
                  >You must entrer the room's name</span>
                </p>
              </b-field>

              <b-field
                label="Type of room"
                :addons="false"
                :type="{'is-danger': errors.has('public')}"
              >
                <b-radio
                  v-model="typeOfRoom"
                  name="public"
                  native-value="public"
                  v-validate="'required'"
                >Public</b-radio>
                <b-radio
                  v-model="typeOfRoom"
                  name="private"
                  native-value="private"
                  v-validate="'required'"
                >Private</b-radio>
                <span v-show="errors.has('public')" class="help is-danger">You must select an option</span>
              </b-field>

              <b-field
                label="Create a password to make your room private"
                v-if="typeOfRoom === 'private'"
              >
                <p class="control has-icon has-icon-right">
                  <input
                    name="password"
                    v-model="password"
                    v-validate="'required|min:8'"
                    :class="{'input': true, 'is-danger': errors.has('password') }"
                    type="password"
                    placeholder="password"
                  />
                  <span
                    v-show="errors.has('password')"
                    class="help is-danger"
                  >Password must have 8 characters at least</span>
                </p>
              </b-field>

              <button type="submit" class="button is-primary">Create a room</button>
            </form>
          </div>
        </transition>
      </div>
      <button class="modal-close is-large" aria-label="close" @click="isModal = false"></button>
    </div>
  </div>
</template>

<script>
import axiosRequest from "../../axiosRequests";
export default {
  name: "HelloWorld",
  data() {
    return {
      isModal: false,
      username: null,
      roomName: null,
      password: null,
      typeOfRoom: null,
      errorMessage: null
    };
  },
  methods: {
    validateBeforeSubmit() {
      this.$validator.validateAll().then(result => {
        if (result) {
          const data = {
            name: this.roomName,
            type: this.typeOfRoom,
            admin: this.username,
            password: this.password
          };
          axiosRequest
            .createRoom(data)
            .then(response => {
              this.playSuccess(response);
            //   if (this.typeOfRoom === "public") {
            //   this.$router.push(`/public/room?name=${this.roomName}&user=${this.username}` )
            // }
            // else {
            //   this.$store.state.isPassChecked = true;
            //   this.$router.push(`/private/room?name=${this.roomName}&user=${this.username}` )
            //   }
            this.$store.state.isPassChecked = true;
            this.$router.push(`/room?name=${this.roomName}&user=${this.username}`) 
            })
            .catch(error => {
              this.errorMessage = error;
              this.username = this.roomName = this.password = this.typeOfRoom = null;
            });
          return;
        }
        this.$toast.open({
          message: "Form is not valid !",
          type: "is-danger",
          position: "is-bottom"
        });
      });
    },
    playError(val) {
      this.$toast.open({
        message: val,
        type: "is-danger",
        position: "is-bottom",
        duration: 4000
      });
    },
    playSuccess(val) {
      this.$toast.open({
        message: val,
        type: "is-success",
        position: "is-bottom",
        duration: 4000
      });
    }
  },
  computed : {
    isConnected() {
      return this.$store.getters.isConnected;
    },
    suscribtion() {
      return this.$store.getters.suscribtion;
    }
  }
};
</script>

