<template>
  <div>
    <!-- Any other Bulma elements you want -->
    <transition name="custom-fade-in-left" enter-active-class="animated bounceInLeft">
      <div class="box">
        <p class="has-text-centered" :style="{color : 'red'}">{{errorMessage}}</p>
        <br />
        <!-- FORM IF THERE IS NO ROOM HISTORY -->
        <form @submit.prevent="validateBeforeSubmit">
          <b-field label="Email">
            <p class="control has-icon has-icon-right">
              <input
                name="email"
                v-model="email"
                v-validate="'required|email'"
                :class="{'input': true, 'is-danger': errors.has('email') }"
                type="text"
                placeholder="Email"
              />
              <i v-show="errors.has('email')" class="fa fa-warning"></i>
              <span v-show="errors.has('email')" class="help is-danger">You must enter a valid email</span>
            </p>
          </b-field>

          <b-field label="Password">
            <p class="control has-icon has-icon-right">
              <input
                name="password"
                v-model="password"
                v-validate="'required'"
                :class="{'input': true, 'is-danger': errors.has('password') }"
                type="password"
                placeholder="Password"
              />
              <i v-show="errors.has('password')" class="fa fa-warning"></i>
              <span
                v-show="errors.has('password')"
                class="help is-danger"
              >You must entrer a password</span>
            </p>
          </b-field>

          <div class="flex-justify">
            <button type="submit" class="button is-primary">Log in</button>
            <span>Forgotten password ?</span>
          </div>
        </form>
      </div>
    </transition>
  </div>
</template>

<script>
import axiosRequest from "../../axiosRequests";
export default {
  props : ['setModal'],
  name: "HelloWorld",
  data() {
    return {
      password: null,
      email: null,
      errorMessage: null
    };
  },
  methods: {
    validateBeforeSubmit() {
      this.$validator.validateAll().then(result => {
        if (result) {
          const data = {
            email: this.email,
            password: this.password
          };
          axiosRequest
            .login(data)
            .then(response => {
              this.email = this.password = null;
              this.playSucces(response.success);
              this.setModal(false);
              this.$store.state.isConnected = true;
              this.$store.state.user = response.user
            })
            .catch(error => {
              this.email = this.password = null;
              this.playError(error);
            });
          return;
        }
        this.email = this.password = null;
        this.$buefy.toast.open({
          message: "Form is not valid !",
          type: "is-danger",
          position: "is-bottom"
        });
      });
    },
    playSucces(val) {
      this.$buefy.toast.open({
        message: val,
        type: "is-success",
        position: "is-bottom",
        duration: 4000
      });
    },
    playError(val) {
      this.$buefy.toast.open({
        message: val,
        type: "is-danger",
        position: "is-bottom",
        duration: 4000
      });
    }
  }
};
</script>

