import axios from "axios";
import store from './store';

class axiosRequest {
  static createRoom(data) {
    return new Promise((resolve, reject) => {
      axios
        .post("/api/createRoom", data)
        .then(response => {
          if (!response.data.error) {
            resolve(response.data.success);
          } else {
            reject(response.data.error);
          }
        })
        .catch(() => reject("Please, try again later"));
    });
  }
  static joinRoom(data) {
    return new Promise((resolve, reject) => {
      axios.post("/api/joinRoom", data).then(response => {
        if (!response.data.error) {
          resolve(response.data);
        } else {
          reject(response.data.error);
        }
      });
    });
  }
  static login(data) {
    return new Promise((resolve, reject) => {
      axios.post("/api/login", data).then(response => {
        if (!response.data.error) {
          resolve(response.data);
        } else {
          reject(response.data.error);
        }
      });
    });
  }
  static logOut() {
    axios.get("/api/logout").then(response => {
      if (!response.data.error) {
        store.state.isConnected = false;
        localStorage.removeItem("cookies");
      }
    });
  }

  static deleteSub() {
    return new Promise((resolve, reject) => {
      axios.delete("/api/delete/subscription")
      .then(response => {
        if (!response.data.error) {
          resolve(response.data.success);
        } else {
          reject(response.data.error);
        }
      });
    });
  }
  
  static createUser(data) {
    return new Promise((resolve, reject) => {
      axios.post("/api/user/create-user", data).then(response => {
        if (!response.data.error) {
          resolve(response.data);
        } else {
          reject(response.data.error);
        }
      });
    });
  }
}

export default axiosRequest;
