<template>
  <div>
    <button class="button" @click="openModal">Join a room</button>
    <div :class="['modal', {'is-active' : isModal}]">
      <div class="modal-background"></div>
      <div class="modal-content">
        <!-- Any other Bulma elements you want -->
        <transition name="custom-fade-in-left" enter-active-class="animated bounceInLeft">
          <div class="box" v-if="isModal">
            <p class="has-text-centered" :style="{color : 'red'}">{{errorMessage}}</p>
            <br />
            <!-- FORM IF THERE IS NO ROOM HISTORY -->
            <form @submit.prevent="validateBeforeSubmit" autocomplete="off" v-if="!hasCookies">
              <b-field label="Username" v-if="!isPrivate">
                <p class="control has-icon has-icon-right">
                  <input
                    name="username"
                    v-model="username"
                    v-validate="'required'"
                    :class="{'input': true, 'is-danger': errors.has('username') }"
                    type="text"
                    placeholder="username"
                  />
                  <i v-show="errors.has('username')" class="fa fa-warning"></i>
                  <span
                    v-show="errors.has('username')"
                    class="help is-danger"
                  >You must entrer an username</span>
                </p>
              </b-field>

              <b-field label="Name of the room" v-if="!isPrivate">
                <p class="control has-icon has-icon-right">
                  <input
                    name="roomName"
                    v-model="roomName"
                    v-validate="'required'"
                    :class="{'input': true, 'is-danger': errors.has('roomName') }"
                    type="text"
                    placeholder="name of the room"
                  />
                  <i v-show="errors.has('roomName')" class="fa fa-warning"></i>
                  <span
                    v-show="errors.has('roomName')"
                    class="help is-danger"
                  >You must entrer the room's name</span>
                </p>
              </b-field>

              <b-field label="Enter password" v-if="isPrivate">
                <p class="control has-icon has-icon-right">
                  <input
                    name="password"
                    v-model="password"
                    v-validate="'required'"
                    :class="{'input': true, 'is-danger': errors.has('password') }"
                    type="password"
                    placeholder="Password"
                  />
                  <i v-show="errors.has('password')" class="fa fa-warning"></i>
                  <span
                    v-show="errors.has('password')"
                    class="help is-danger"
                  >You must entrer a password</span>
                </p>
              </b-field>

              <button type="submit" class="button is-primary">Se connecter</button>
            </form>

            <!-- ROOM HISTORY -->

            <div id="history" class="has-text-centered" v-if="hasCookies">
              <button class="button is-primary" @click="hasCookies = false">Join a new room</button>
              <hr />
              <p>or re-join a visited room :</p>
              <br />
              <ul>
                <li v-for="(room, index) in roomHistory" :key="index" class="margin-bottom">
                  <button class="button is-small" @click="joinHistoryRoom(room)" v-id="room != '__stripe_mid' && room !='__stripe_sid'">{{room}}</button>
                </li>
              </ul>
            </div>
          </div>
        </transition>
      </div>
      <button class="modal-close is-large" aria-label="close" @click="closeModal"></button>
    </div>
  </div>
</template>

<script>
import axiosRequest from "../../axiosRequests";
export default {
  name: "HelloWorld",
  data() {
    return {
      isModal: false,
      hasCookies: false,
      username: null,
      roomName: null,
      isPrivate: false,
      password: null,
      checked: null,
      errorMessage: null,
      roomHistory: []
    };
  },
  methods: {
    openModal() {
      //check for cookies
      let cookies = this.$cookies.keys();
    
      //just cookies about rooms history
      if (cookies.indexOf("login") >= 0) {
        cookies.splice(cookies.indexOf("login"), 1);
      } 
      if (cookies.length > 0) {
        this.hasCookies = true;
        this.roomHistory = cookies;
      }
      this.isModal = true;
    },
    joinHistoryRoom(roomName) {
      this.hasCookies = false;
      this.roomName = roomName;
    },
    closeModal() {
      this.isModal = this.isPrivate = false;
      this.username = this.roomName = this.password = this.errorMessage = null;
    },
    validateBeforeSubmit() {
      this.$validator.validateAll().then(result => {
        if (result) {
          const data = {
            name: this.roomName,
            username: this.username,
            password: this.password
          };
          axiosRequest
            .joinRoom(data)
            .then(response => {
              if (response.private) {
                this.errorMessage = response.private;
                this.isPrivate = true;
              } else if (response.wrongPass) {
                this.errorMessage = response.wrongPass;
                this.password = null;
              }
              // else if (response.isPublic) {
              //   this.$store.state.isPassChecked = true;
              //   this.$router.push(
              //     `/public/room?name=${this.roomName}&user=${this.username}`
              //   );
              // } else {
              //   this.$store.state.isPassChecked = true;
              //   this.$router.push(
              //     `/private/room?name=${this.roomName}&user=${this.username}`
              //   );
              // }
              else {
                this.$store.state.ip = response.ip;
                this.$store.state.isPassChecked = true;
                this.$router.push(
                  `/room?name=${this.roomName}&user=${this.username}`
                );
              }
            })
            .catch(error => {
              this.errorMessage = error;
              this.username = this.roomName = this.password = null;
            });
          return;
        }
        this.username = this.roomName = this.password = null;
        this.$buefy.toast.open({
          message: "Form is not valid !",
          type: "is-danger",
          position: "is-bottom"
        });
      });
    },
    playError(val) {
      this.$buefy.toast.open({
        message: val,
        type: "is-danger",
        position: "is-bottom",
        duration: 4000
      });
    }
  }
};
</script>

