<template>
  <nav class="navbar is-spaced" role="navigation" aria-label="main navigation">
    <div class="navbar-brand">
      <figure class="image is-64x64" href="/">
        <img src="../../assets/cube.png" />
      </figure>

      <a
        role="button"
        class="navbar-burger burger"
        aria-label="menu"
        aria-expanded="false"
        data-target="navbarBasicExample"
        @click="isActive = !isActive"
        :class="{'is-active' : isActive}"
      >
        <span aria-hidden="true"></span>
        <span aria-hidden="true"></span>
        <span aria-hidden="true"></span>
      </a>
    </div>

    <div id="navbarBasicExample" class="navbar-menu" :class="{'is-active' : isActive}">
      <div class="navbar-end">
        <div class="navbar-item">
          <div class="buttons">
            <a class="button is-join" v-if="!isConnected" @click="modalActive = true">Log in</a>
            <a class="button is-join" v-if="isConnected" @click="logOut" >Log out</a>
            <router-link v-if="!isConnected || suscribtion !== 'true' " tag="a" to="/pricing" class="button is-create">
             Subscribe
            </router-link>
            <a class="button is-create" v-if="isConnected && suscribtion == 'true' " @click="unsuscribe" >Unsubscribe</a>
          </div>
        </div>
      </div>
    </div>

    <div class="modal" :class="{'is-active' : modalActive}">
  <div class="modal-background"></div>
  <div class="modal-content">
    <!-- Any other Bulma elements you want -->
    <app-login :setModal="setModal"> </app-login>
  </div>
  <button class="modal-close is-large" aria-label="close" @click="modalActive = false"></button>
</div>

<!-- LOADER -->
    <b-loading :is-full-page="true" :active.sync="isLoading" :can-cancel="true"></b-loading>
    
  </nav>
</template>

<script>
import login from './Login'
import axiosRequest from '../../axiosRequests'
export default {
components : {
    "app-login" : login
},
  data() {
    return {
      isActive: false,
      modalActive : false,
      isLoading: false,
    };
  },
  methods : {
    setModal(response) {
      this.modalActive = response
    },
    unsuscribe() {
      this.isLoading = true
      axiosRequest.deleteSub()
      .then(response => {
        this.isLoading = false;
        this.playSucces(response);
        this.$store.state.user.suscribtion = "false";
      })
      .catch(error => {
        this.isLoading = false;
        this.playError(error);
        })
    },
    logOut() {
      axiosRequest.logOut()
    },
     playSucces(val) {
      this.$buefy.toast.open({
        message: val,
        type: "is-success",
        position: "is-bottom",
        duration: 6000
      });
    },
    playError(val) {
      this.$buefy.toast.open({
        message: val,
        type: "is-danger",
        position: "is-bottom",
        duration: 4000
      });
    }
  },
  computed : {
    isConnected() {
      return this.$store.getters.isConnected
    },
    suscribtion() {
      return this.$store.getters.suscribtion
    }
  }
};
</script>

